@tailwind base;
@tailwind components;
@tailwind utilities;

/* === RÃ©glage hauteur panneau "Avis" === */
:root{
  --reviews-offset-mobile: 2rem;   /* distance au-dessus des avis sur mobile */
  --reviews-offset-desktop: -8rem;/* distance au-dessus des avis sur desktop */
  --reviews-offset: var(--reviews-offset-mobile);
}

@media (min-width: 768px){
  :root{ --reviews-offset: var(--reviews-offset-desktop); }
}
